
import {mapState,mapMutations} from 'vuex';
export default {
    data(){
        return{
            fullScreen: false,//
            today: '',
            buttonList: [
                {
                    value: 1,
                    text: ''
                },
                {
                    value: 2,
                    text: ''
                },
                {
                    value: 3,
                    text: ''
                },
                {
                    value: 4,
                    text: ''
                },
                {
                    value: 5,
                    text: ''
                },
            ]
        }
    },
    mounted(){
        document.addEventListener('fullscreenchange',this.changeScreen);
    },
    methods: {
        // 
        toToday(){
            let today = document.getElementById('today-ganteview-column');
            let ganteview = document.getElementsByClassName('ganteview')[0];
            if(ganteview){
                let left = today.offsetLeft - ganteview.clientHeight/2;
                if(left<0){
                    left = 0
                }
                ganteview.scrollLeft = left;
            }
        },
        //
        trickMode(item) {
            this.init({time_mode: item}, false);
        },
        //
        op_time_mode(mode){
            this.time_mode+=mode;
            if(this.time_mode<=1){
                this.time_mode = 1;
            }
            if(this.time_mode>=5){
                this.time_mode=5
            }
            this.init({time_mode: this.time_mode}, false);
        },
        // 
        toFullScreen(){
            let element = document.documentElement;
            // 
            // 
            if (this.fullScreen) {
                if (document.exitFullscreen) {
                    document.exitFullscreen();
                } else if (document.webkitCancelFullScreen) {
                    document.webkitCancelFullScreen();
                } else if (document.mozCancelFullScreen) {
                    document.mozCancelFullScreen();
                } else if (document.msExitFullscreen) {
                    document.msExitFullscreen();
                }
                console.log('');
            } else {    // 
                if (element.requestFullscreen) {
                    element.requestFullscreen();
                } else if (element.webkitRequestFullScreen) {
                    element.webkitRequestFullScreen();
                } else if (element.mozRequestFullScreen) {
                    element.mozRequestFullScreen();
                } else if (element.msRequestFullscreen) {
                    // IE11
                    element.msRequestFullscreen();
                }
                console.log('');
            }
            // 
            this.fullScreen = !this.fullScreen;
        },
        changeScreen(){
            if (document.fullscreenElement) {
                this.fullScreen = true;
            } else {
                this.fullScreen = false;
            }
            setTimeout(()=>{
                this.init({}, false);
            },500)
        },
    },
    destroyed(){
        document.removeEventListener('fullscreenchange',this.changeScreen);
    }
}



